<template>
    <div class="app">
        <post-form 
            :posts="posts"
            @create="createPost"
        />
        <post-list 
            :posts="posts"
            
        />
    </div>
</template>

<script>
import PostForm from './components/PostForm.vue'
import PostList from './components/PostList.vue'
export default {
    components: {
        PostList, PostForm
    },
    data() {
        return {
            posts: []
        }
    },
    methods: {
    createPost(post) {
        this.posts.push(post)
    }
    }
}
</script>

<style>
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.app {
    padding: 20px;
}
</style>